@font-face{font-family:"OngleafParkDahyun";src:url("../../../public/fonts/온글잎_박다현체.ttf") format("truetype");font-weight:normal;font-style:normal;font-display:swap}body{font-family:"OngleafParkDahyun","Pretendard",sans-serif,sans-serif}*{box-sizing:border-box;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;margin:0;padding:0}html,body{margin:0;padding:0;font-size:16px;line-height:1.6;background-color:#f4f4f4;color:#1f2937;scroll-behavior:smooth}a{color:inherit;text-decoration:none}a:hover{text-decoration:underline}ul,ol{list-style:none;margin:0;padding:0}button{background:none;border:none;cursor:pointer;font-family:inherit;padding:0}img{max-width:100%;display:block}.header{position:fixed;top:0;left:0;right:0;width:100%;margin:0 auto;padding:.8vw 5vw;z-index:1000;display:flex;justify-content:space-between;align-items:center;font-size:.9rem;color:#374151;backdrop-filter:blur(10px);height:60px}.header .header-actions{display:flex;align-items:center;gap:1rem;height:100%}.header .header-actions button{display:flex;align-items:center;justify-content:center;padding:.4rem;color:inherit;background:rgba(0,0,0,0);border:none;cursor:pointer}.header.scrolled{background:rgba(0,0,0,.85);box-shadow:0 2px 8px rgba(0,0,0,.2);color:#fff}.header.scrolled nav a,.header.scrolled nav button{color:#fff}.header.scrolled div button{color:#fff}.header.scrolled .header-actions button{color:#fff}.header .logo a{font-size:1.8rem;font-weight:700;letter-spacing:2px;position:relative;background:linear-gradient(135deg, #3a7bd5, #00d2ff);-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0)}.header .logo a::after{content:"";position:absolute;width:6px;height:6px;border-radius:50%;background:#00d2ff;bottom:5px;right:-10px;animation:pulse 2s infinite}.header nav{display:flex;align-items:center;gap:2rem}.header nav .nav-links{display:flex;align-items:center;gap:2rem}.header nav .nav-links li{position:relative}.header nav .nav-links li a,.header nav .nav-links li button{font-size:1rem;position:relative;padding:.5rem 0;font-weight:500;text-decoration-line:none;list-style:none}.header nav .nav-links li a::after,.header nav .nav-links li button::after{content:"";position:absolute;bottom:0;left:0;width:0;height:2px;transition:width .3s ease}.header nav .nav-links li a:hover::after,.header nav .nav-links li button:hover::after{width:100%}.header nav .nav-links li .buy-button{background:linear-gradient(135deg, #3a7bd5, #00d2ff);color:#fff;border:none;padding:.6rem 1.5rem;border-radius:30px;font-weight:600;letter-spacing:1px;transition:all .3s ease;box-shadow:0 4px 15px rgba(0,210,255,.2)}.header nav .nav-links li .buy-button:hover{transform:translateY(-2px);box-shadow:0 6px 20px rgba(0,210,255,.3)}.header nav .nav-links li .buy-button:active{transform:translateY(1px)}@keyframes pulse{0%{transform:scale(0.95);box-shadow:0 0 0 0 rgba(0,210,255,.7)}70%{transform:scale(1);box-shadow:0 0 0 5px rgba(0,210,255,0)}100%{transform:scale(0.95);box-shadow:0 0 0 0 rgba(0,210,255,0)}}.active-nav-item::after{width:100% !important}.footer{width:100%;padding:16px 24px;background-color:#333;color:#fff;text-align:center;font-size:.9rem;line-height:1.4;margin-top:auto}.footer a{color:#fff;text-decoration:none}.footer a:hover{text-decoration:underline}.app-container{margin-top:60px;display:flex;flex-direction:column;block-size:100%;min-height:100vh}.container{margin:2rem;padding:2rem}html.dark body{background-color:#121212;color:#e0e0e0}html.dark .header{background-color:rgba(0,0,0,.85);color:#fff}html.dark .header a,html.dark .header button{color:#fff}html.dark .header .logo a::after{background:#374151}html.dark .card{background-color:#1e1e1e;border:1px solid #333;color:#eee}html.dark .buy-button{background:linear-gradient(135deg, #6366f1, #8b5cf6);color:#fff}